.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.8rem 10rem;
  background-color: #21212150;
  backdrop-filter: blur(6px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1000;
}

.header-container nav{
  display: flex;
  align-items: center;
  gap: 1.8rem;
}

.header-container nav a{
  color: #FFFF;
  padding: 0.6rem;
  font-family: 'Red Hat Display', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  transition: filter 0.25s;
}

.header-container nav a.button{
  padding: 0.6rem 2rem;
}

.header-container nav a:hover{
  filter: brightness(0.6);
}

.header-container .menu-container{
  cursor: pointer;
  padding: 0.6rem 0;
}

.header-container .menu{
  width: 2rem;
  height: 0.2rem;
  background: #FFFF;
  position: relative;
  cursor: pointer;
  display: none;
}

.header-container .menu:before{
  bottom: 0.5rem;
}

.header-container .menu:after{
  top: 0.5rem;
}

.header-container .menu.active:before{
  bottom: 0;
  transform: rotate(45deg);
}

.header-container .menu.active:after{
  top: 0;
  transform: rotate(135deg);
}

.header-container .menu.active{
  background-color: rgba(0, 0, 0, 0);
}

.header-container .menu:before, 
.header-container .menu:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 0.2rem;
  background: #FFFF;
  cursor: pointer;
  transition: .6s;
}

.header-container input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
  outline: none;
}

.header-container label {
  cursor: pointer;
  text-indent: -9999px;
  width: 55px;
  height: 30px;
  background: var(--green);
  display: block;
  justify-content: center;
  align-items: center;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  border-radius: 100px;
  position: relative;
  margin-left: auto;
  right: 10px;
}

@media only screen and (max-width: 800px) {
  .header-container label {
    position: relative;
  }
}

.header-container label:after {
  content: '';
  background: #FFF;
  width: 20px;
  height: 20px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 4px;
  transition: cubic-bezier(0.68, -0.55, 0.27, 01.55) 320ms;
}

.header-container input:checked + label {
  background: var(--pink);
}

.header-container input:checked + label:after {
  left: calc(100% - 5px);
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
}

@media (max-width: 960px){
  .header-container{
    padding: 1.8rem 3rem;
  }

  .header-container .menu{
    display: block;
  }

  .header-container nav {
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: var(--blue);
    top: 0;
    left: 0;
    transition: opacity 0.25s;
    background-color: var(--green);
  }

  .header-container nav a.button{
    background-color: var(--pink);
  }

  .header-container nav.active{
    opacity: 1;
    visibility: visible;
  }
} 