.footer-container {
  background-color: #2b2b2b;
  padding: 3rem 15rem;
  margin-top: 10rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-container .logo{
  font-size: 2.8rem;
}

.footer-container p{
  letter-spacing: 0.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.footer-container p img{
  width: 2.6rem;
  animation: spinning 5s infinite linear;
}

.footer-container .social-media{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.footer-container .social-media img,
.footer-container .social-media span{
  font-size: 3rem;
  width: 3rem;
}

@keyframes spinning {
  0%{
    transform: rotate(0);
  }
  100%{
    transform: rotate(360deg);
  }
}

@media(max-width: 800px){
  .footer-container{
    padding: 4rem 10rem;
    flex-direction: column;
    gap: 2rem;
    text-align: center;
  }
}

@media(max-width: 600px){
  .footer-container{
    padding: 4rem 1rem;
  }
  
  .footer-container p{
    font-size: 1.2rem;
  }
} 