.form-container {
  margin-top: 3rem;
  display: grid;
  place-items: center;
}

.form-container h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.form-container form {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
}

.form-group {
  margin-bottom: 1.5rem;
  width: 100%;
}

.form-control {
  width: 100%;
  padding: 1rem 2rem;
  border-radius: 1.6rem;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.05);
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
  font-size: 1.6rem;
}

.form-control:focus {
  border-color: #646cff;
  box-shadow: 0 0 0 2px rgba(100, 108, 255, 0.2);
}

.form-control.error {
  border-color: #ff4d4f;
  background-color: rgba(255, 77, 79, 0.05);
}

.error-message {
  color: #ff4d4f;
  font-size: 1.2rem;
  margin-top: 0.5rem;
  padding-left: 1rem;
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

.form-container form input::placeholder,
.form-container form textarea::placeholder {
  color: #fff;
}

.form-container form textarea {
  height: 20rem;
  overflow-y: auto;
  resize: none;
}

.form-container form button {
  position: relative;
  padding: 1.2rem 4rem;
  text-transform: uppercase;
  transition: all 0.3s ease;
  cursor: pointer;
  background: transparent;
  color: white;
  border: 1px solid #646cff;
  border-radius: 1.6rem;
  font-weight: 600;
  font-size: 1.4rem;
  letter-spacing: 1px;
  overflow: hidden;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 2rem auto 0;
  min-width: 20rem;
}

.form-container form button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(100, 108, 255, 0.2);
  transition: width 0.3s ease;
  z-index: -1;
}

.form-container form button:hover:not(:disabled)::before {
  width: 100%;
}

.form-container form button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  border-color: rgba(255, 255, 255, 0.3);
}

.spinner {
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Success state */
.form-success {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(46, 204, 113, 0.1);
  border: 1px solid rgba(46, 204, 113, 0.3);
  border-radius: 1.6rem;
  margin-top: 2rem;
  animation: fadeIn 0.5s ease-in-out;
}

.form-success h3 {
  color: #2ecc71;
  margin-bottom: 2rem;
  font-size: 2.4rem;
}

.form-success button {
  background: transparent;
  border: 1px solid #2ecc71;
  color: #2ecc71;
  padding: 1rem 2.5rem;
  border-radius: 1.6rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.form-success button:hover {
  background: rgba(46, 204, 113, 0.1);
}

@keyframes spin {
  to {
    transform: translateY(-50%) rotate(360deg);
  }
}

@media (max-width: 740px) {
  .form-container form {
    width: 100%;
  }
  
  .form-container {
    padding: 0 2rem;
  }
  
  .form-control {
    padding: 1.2rem 1.8rem;
    font-size: 1.4rem;
  }
  
  .form-container form button {
    width: 100%;
    padding: 1.4rem;
  }
}

.form-success {
  margin: 10rem auto 0;
  max-width: 60rem;
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 1.6rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.form-success button {
  border-radius: 0.6rem;
  padding: 1rem;
  margin-top: 0.8rem;
  text-transform: uppercase;
  text-align: center;
  color: #fbfbfb;
}
